export default class DateTimeUtil {
    static getArrayMonth() {
        return [
            { name: 'Thng 1', id: 1 },
            { name: 'Thng 2', id: 2 },
            { name: 'Thng 3', id: 3 },
            { name: 'Thng 4', id: 4 },
            { name: 'Thng 5', id: 5 },
            { name: 'Thng 6', id: 6 },
            { name: 'Thng 7', id: 7 },
            { name: 'Thng 8', id: 8 },
            { name: 'Thng 9', id: 9 },
            { name: 'Thng 10', id: 10 },
            { name: 'Thng 11', id: 11 },
            { name: 'Thng 12', id: 12 }
        ]
    }

    static getArrayYear() {
        let array = [];
        for (var i = 1920; i <= 2020; i++) {
            array.push(i);
        }
        return array;
    }

    static getArrayDay(month, year) {
        let maxDay = new Date(year, month, 0).getDate();
        let array = [];
        for (var i = 1; i <= maxDay; i++) {
            array.push(i);
        }
        return array;
    }

    static toString(date) {
        if (!date) return '';
        let mon =  date.getMonth() + 1;
        if (mon < 10) {
            mon = '0' + mon;
        }
        let day = date.getDate();
        if (day < 10) {
            day = '0' + day;
        }
        return [date.getFullYear(), mon, day].join('-');
    }
}